import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Persik from './panels/Persik';


import { Panel, ConfigProvider } from '@vkontakte/vkui';
import vkuiConnect from '@vkontakte/vkui-connect';



class App extends React.Component {

	state = {
    activePanel: 'main',
    history: ['main']
  }
  
  goBack = () => {
    const history = [...this.state.history];
    history.pop();
    const activePanel = history[history.length - 1];
    if (activePanel === 'main') {
      vkuiConnect.send('VKWebAppDisableSwipeBack');
    }
    this.setState({ history, activePanel });
  }
  
  goForward = (activePanel) => {
    const history = [...this.state.history];
    history.push(activePanel);
    if (this.state.activePanel === 'main') {
      vkuiConnect.send('VKWebAppEnableSwipeBack');
    }
    this.setState({ history, activePanel });
  }


	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data });
					break;
				default:
					console.log(e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
	}

	go = (e) => {
		this.goForward(e ? e : "persik")
	};

	render() {
		return (
			<View activePanel={this.state.activePanel}>
				
			</View>
		);

		return <ConfigProvider isWebView={true}>
        <View 
          onSwipeBack={this.goBack} 
          history={this.state.history} 
          activePanel={this.state.activePanel}
        >
          <Home id="home" fetchedUser={this.state.fetchedUser} go={() => this.go()} />
			<Persik id="persik" go={this.go} />
        </View>
      </ConfigProvider>
	}
}

export default App;
